import React, { Fragment } from 'react';
import { Carousel } from 'react-responsive-carousel';
import { Image } from 'react-bootstrap';
import '../styles/caurosel.css';

export default ({ customCarouselData }) => (
  <Carousel
    showStatus={false}
    showIndicators={false}
    showThumbs={false}
    infiniteLoop
    autoPlay
    interval={3000}
  >
    {
      customCarouselData.map(el => (
        <Fragment>
          <div className="caurosel-text">
            <Image
              src={el.img}
              alt={el.alt}
              fluid
            />
            <h3>{el.label}</h3>
            <p>{el.text}</p>
          </div>
        </Fragment>
      ))
    }
  </Carousel>
);
